import Vue from 'vue';
export default {
  props: [],
  components: {},
  data: function data() {
    return {
      loading: true,
      loadingAction: false,
      statusDeal: false,
      keypayStatusText: '',
      dossierDetail: {
        serviceName: '',
        dossierNo: '',
        govAgencyName: '',
        paymentFee: '',
        paymentAmount: '',
        transId: '',
        goodCode: '',
        paymentPortal: ''
      },
      keypayStatusCode: {
        "00": "Thnh cng",
        "01": "i l khng tn ti trong h thng",
        "02": "Chui m ha khng hp l",
        "03": "M giao dch i l khng hp l",
        "04": "Khng tm thy giao dch trong h thng",
        "05": "M dch v khng hp l",
        "06": "Li xc nhn giao dch: giao dch  c xc nhn (thnh cng/ khng thnh cng trc  v khng th xc nhn li)",
        "07": "M quc gia khng hp l",
        "08": "Khng nhn c thng ip tr v t Ngn Hng",
        "09": "M t n hng khng hp l",
        "10": "M n hng khng hp l",
        "11": "S tin khng hp l",
        "12": "Ph vn chuyn khng hp l",
        "13": "Thu khng hp l",
        "14": "i l cha c cu hnh ph",
        "15": "Sai m Ngn hng",
        "16": "S tin thanh ton ca i l khng nm trong khong cho php",
        "17": "Ti khon khng  tin",
        "18": "Khch hng  Hy giao dch",
        "19": "Thi gian thanh ton khng hp l",
        "20": "Kiu nhn m OTP khng hp l",
        "21": "M OTP sai",
        "25": "Nhp sai thng tin ch th ln 1",
        "26": "Nhp sai thng tin ch th ln 2",
        "27": "Nhp sai thng tin ch th ln 3",
        "30": "Phin bn khng hp l",
        "31": "M lnh khng hp l",
        "32": "Loi tin t khng hp l",
        "33": "Ngn ng khng hp l",
        "34": "Thng tin thm (desc 1) khng hp l",
        "35": "Thng tin thm (desc 2) khng hp l",
        "36": "Thng tin thm (desc 3) khng hp l",
        "37": "Thng tin thm (desc 4) khng hp l",
        "38": "Thng tin thm (desc 5) khng hp l",
        "39": "Chui tr v - Return URL khng hp l",
        "40": "Loi th khng hp l",
        "41": "Th nghi vn (th nh mt, hot card)",
        "54": "Th ht hn",
        "57": "Cha ng k dch v thanh ton trc tuyn",
        "61": "Qu hn mc giao dch trong ngy",
        "62": "Th b kha",
        "65": "Qu hn mc 1 ln giao dch",
        "97": "Ngn hng cha sn sng",
        "98": "Giao dch khng",
        "99": "Li h thng"
      }
    };
  },
  computed: {},
  created: function created() {
    var vm = this;
    vm.$nextTick(function () {
      var vm = this;
      var query = vm.$router.history.current.query;
      var responseCode = query.hasOwnProperty('response_code') ? query.response_code : '';
      var dossierId = query.hasOwnProperty('dossierId') ? query.dossierId : '';
      var referenceUidQuery = query.hasOwnProperty('referenceUid') ? query.referenceUid : '';
      var actionCode = query.hasOwnProperty('actionCode') ? query.actionCode : '';

      if (dossierId) {
        if (responseCode === '00') {
          vm.statusDeal = true;
        } else {
          vm.statusDeal = false;
          vm.keypayStatusText = vm.keypayStatusCode['responseCode'] ? vm.keypayStatusCode['responseCode'] : '';
        }

        vm.$store.dispatch('getDetailDossier', referenceUidQuery).then(function (resultDossier) {
          vm.dossierDetail['serviceName'] = resultDossier.serviceName;
          vm.dossierDetail['dossierNo'] = resultDossier.dossierNo;
          vm.dossierDetail['govAgencyName'] = resultDossier.govAgencyName;
          vm.dossierDetail['transId'] = query.transId;
          vm.dossierDetail['goodCode'] = query.goodCode;
          vm.dossierDetail['paymentPortal'] = query.paymentPortal;
          vm.dossierDetail['paymentAmount'] = query.net_cost;
          var filter = {
            dossierId: resultDossier.dossierId,
            referenceUid: !referenceUidQuery ? resultDossier.referenceUid : referenceUidQuery
          };
          var filterPayment = {
            dossierId: !referenceUidQuery ? resultDossier.referenceUid : referenceUidQuery
          };

          if (vm.statusDeal === true) {
            vm.$store.dispatch('putPayments', filter).then(function (result) {
              vm.$store.dispatch('loadDossierPayments', filterPayment).then(function (result) {
                vm.dossierDetail['paymentFee'] = vm.getEPaymentProfile(result.epaymentProfile).paymentFee;
              }).catch(function (reject) {});

              if (actionCode) {
                var fiter2 = {
                  dossierId: !referenceUidQuery ? resultDossier.referenceUid : referenceUidQuery,
                  actionCode: actionCode
                };
                vm.$store.dispatch('processDossierRouter', fiter2).then(function (result) {}).catch(function () {});
              }
            }).catch(function (reject) {});
          } else {
            vm.$store.dispatch('loadDossierPayments', filterPayment).then(function (result) {
              vm.dossierDetail['paymentFee'] = vm.getEPaymentProfile(result.epaymentProfile).paymentFee;
            }).catch(function (reject) {});
          }
        });
      }
    });
  },
  watch: {},
  methods: {
    currency: function currency(value) {
      if (value) {
        return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.') ? value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, '.') : 0;
      }

      return 0;
    },
    goBack: function goBack() {
      var vm = this;
      var redirectURL = window.themeDisplay.getLayoutRelativeURL().substring(0, window.themeDisplay.getLayoutRelativeURL().lastIndexOf('\/'));
      var url = redirectURL + '/dich-vu-cong#/';
      window.open(url, '_self');
    },
    getEPaymentProfile: function getEPaymentProfile(paymentProfile) {
      if (paymentProfile) {
        try {
          JSON.parse(paymentProfile);
          return JSON.parse(paymentProfile);
        } catch (e) {
          return '';
        }
      } else {
        return '';
      }
    }
  }
};